*,
*::before,
*::after {
  box-sizing: border-box;
}
img {
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
:root {
  --white: #ffffff;
  --white-rgb: 255, 255, 255;
  --gray-lightest: #f2f2f2;
  --gray-lighten: #eaeaea;
  --gray-light: #e6e6e6;
  --gray: #cdcdcd;
  --gray-dark: #666666;
  --gray-darken: #444444;
  --gray-darken--rgb: 68, 68, 68;
  --gray-darkest: #222222;
  --almost-black: #111111;
  --black: #000000;
  --black-rgb: 0, 0, 0;
  --color-main: #68b738;
  --color-main-rgb: 104, 183, 56;
  --color-main-dark: #5eaa2f;
  --color-danger: #ff8282;
  --bg-color-primary: var(--white);
  --bg-color-secondary: var(--gray-lightest);
  --bg-color-tertiary: var(--gray-lighten);
  --border-color: var(--gray-light);
  --text-color-primary: var(--gray-darken);
  --text-color-secondary: var(--black);
  --btn-bg-primary: var(--color-main);
  --btn-bg-primary-hover: var(--color-main-dark);
  --btn-text-primary: var(--white);
  --btn-bg-secondary: var(--gray-lightest);
  --btn-bg-secondary-hover: var(--gray-lighten);
  --btn-text-secondary: var(--gray-darken);
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container {
  min-width: 320px;
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 30px;
  }
}
@media (min-width: 1280px) {
  .container {
    width: 1280px;
    padding: 0 30px;
  }
}
.main-nav__list,
.socials__list,
.programs__list,
.proof__details,
.order-form__list,
.about-product__list,
.products__list,
.product__details {
  margin: 0;
  padding: 0;
  list-style: none;
}
.main-title {
  font-family: "Oswald", "Arial", "Helvetica", sans-serif;
  font-size: 24px;
  font-weight: 400;
  line-height: 1.1;
  color: var(--text-color-secondary);
  text-transform: none;
  position: relative;
  display: block;
  width: 100%;
  margin: 0 0 33px;
  padding: 0;
}
@media (min-width: 768px) {
  .main-title {
    font-size: 36px;
  }
}
.main-title span {
  display: flex;
  width: fit-content;
  padding: 0 20px;
  background-color: var(--bg-color-primary);
}
@media (min-width: 768px) {
  .main-title span {
    margin-left: 30px;
    padding: 0 38px;
  }
}
@media (min-width: 1280px) {
  .main-title span {
    margin-left: 70px;
    padding: 0 40px;
  }
}
.main-title::after {
  position: absolute;
  top: 56%;
  z-index: -1;
  display: block;
  width: 100%;
  height: 2px;
  background-color: var(--btn-bg-primary);
  content: "";
}
@media (min-width: 768px) {
  .main-title::after {
    top: 60%;
  }
}
.button {
  display: block;
  padding: 11px 26px;
  font-family: "Oswald", "Arial", "Helvetica", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.25;
  color: var(--text-color-secondary);
  text-transform: uppercase;
  text-align: center;
  color: var(--btn-text-primary);
  background-color: var(--btn-bg-primary);
  border: none;
  cursor: pointer;
}
.button:hover {
  background-color: var(--btn-bg-primary-hover);
}
.button:active {
  color: rgba(var(--white-rgb), 0.3);
}
.button-secondary {
  background-color: var(--btn-bg-secondary);
  color: var(--btn-text-secondary);
}
.button-secondary:hover {
  background-color: var(--btn-bg-secondary-hover);
}
.button-secondary:active {
  color: rgba(var(--gray-darken--rgb), 0.3);
}
.page {
  position: relative;
  height: 100%;
  font-family: "Arial", "Helvetica", sans-serif;
  font-size: 14px;
  line-height: 1.3;
}
.page__body {
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  color: var(--text-color-primary);
  background-color: var(--bg-color-primary);
}
@media (min-width: 1280px) {
  .page__body:not(.page__body--inner) .main-nav__link {
    color: var(--white);
  }
  .page__body:not(.page__body--inner)::before {
    position: absolute;
    z-index: -1;
    content: "";
    top: 0;
    left: 50%;
    width: 50%;
    height: 694px;
    background-repeat: no-repeat;
    background-size: auto 694px;
    background-image: linear-gradient(to bottom, rgba(var(--color-main-rgb), 0.85) 694px, transparent 694px), url("../img/content/hero-bg-desktop.jpg");
  }
}
@media (min-width: 1280px) and (min-resolution: 1.5dppx) {
  .page__body:not(.page__body--inner)::before {
    background-image: linear-gradient(to bottom, rgba(var(--color-main-rgb), 0.85) 694px, transparent 694px), url("../img/content/hero-bg-desktop@2x.jpg");
  }
}
@media (max-width: 767px) {
  .page__body--inner .page-header {
    border-bottom: 1px solid var(--border-color);
  }
}
.page__body--inner .main-nav__link--current::after {
  background-color: var(--color-main);
}
.page__body--inner .contacts {
  background-color: var(--bg-color-secondary);
}
@media (max-width: 767px) {
  .page__body--inner .contacts {
    margin: 0 -20px;
    padding-left: 20px;
    padding-right: 20px;
  }
}
@media (min-width: 768px) {
  .page__body--inner .contacts {
    margin: 0 -30px;
    padding-left: calc(30px + 40px);
    padding-right: calc(30px + 40px);
  }
}
@media (min-width: 1280px) {
  .page__body--inner .contacts {
    margin: 0;
    padding: 0 80px;
    background-color: var(--bg-color-primary);
  }
}
.page-header {
  position: relative;
  min-height: 65px;
  color: var(--black);
}
@media (max-width: 767px) {
  .page-header {
    padding: 0;
    background-color: var(--bg-color-primary);
  }
}
@media (min-width: 768px) {
  .page-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-height: 100px;
  }
}
@media (min-width: 1280px) {
  .page-header {
    min-height: 141px;
  }
}
.page-header__logo {
  padding: 10px 20px;
}
@media (max-width: 767px) {
  .page-header__logo {
    position: absolute;
    justify-content: space-between;
    width: calc(50% + 70px);
    min-height: 65px;
  }
}
@media (min-width: 768px) {
  .page-header__logo {
    margin: 5px 0 0 -20px;
  }
}
@media (min-width: 1280px) {
  .page-header__logo {
    margin-top: 29px;
  }
}
.logo {
  display: flex;
  align-items: center;
}
.logo[href]:hover {
  opacity: 0.8;
}
.logo[href]:active {
  opacity: 0.6;
}
@media (max-width: 767px) {
  .logo__icon {
    margin-bottom: 5px;
  }
}
@media (min-width: 768px) {
  .logo__icon {
    width: 61px;
    height: 51px;
    margin-right: 11px;
  }
}
@media (min-width: 1280px) {
  .logo__icon {
    width: 71px;
    height: 59px;
    margin-right: 12px;
  }
}
.logo__text {
  width: 101px;
  height: 18px;
  fill: var(--almost-black);
}
@media (max-width: 767px) {
  .logo__text {
    margin-bottom: 3px;
  }
}
@media (min-width: 768px) {
  .logo__text {
    width: 102px;
    height: 20px;
  }
  .logo__text path:last-child {
    fill: var(--gray-darken);
  }
}
@media (min-width: 1280px) {
  .logo__text {
    width: 120px;
    height: 21px;
  }
}
.main-nav {
  font-family: "Oswald", "Arial", "Helvetica", sans-serif;
  font-size: 18px;
  line-height: 1.3;
  color: var(--text-color-secondary);
  text-transform: uppercase;
  display: flex;
  flex-direction: column;
  font-weight: 400;
  text-align: center;
}
@media (max-width: 767px) {
  .main-nav {
    font-size: 20px;
    line-height: 1;
    font-weight: 500;
    background-color: var(--bg-color-primary);
  }
  .main-nav--closed .main-nav__list {
    height: 0;
    opacity: 0;
    visibility: hidden;
  }
  .main-nav--opened .main-nav__list {
    position: absolute;
    top: 65px;
    opacity: 1;
    transition: all linear 0.3s;
    z-index: 10;
  }
  .main-nav--opened .main-nav__toggle::before {
    height: 0;
    margin-top: -1px;
    border-width: 1px;
    transform: rotate(-45deg);
  }
  .main-nav--opened .main-nav__toggle::after {
    height: 0;
    border-width: 1px;
    transform: rotate(45deg);
  }
  .main-nav--no-js .main-nav__list {
    height: auto;
    opacity: 1;
  }
  .main-nav--no-js .main-nav__toggle {
    visibility: hidden;
  }
}
@media (min-width: 768px) {
  .main-nav {
    margin: 10px -10px 0 0;
  }
}
@media (min-width: 1280px) {
  .main-nav {
    margin-top: 27px;
    font-size: 20px;
    color: var(--white);
  }
}
.main-nav__toggle {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  align-self: flex-end;
  width: 64px;
  height: 65px;
  margin: 0;
  padding: 0;
  background-color: var(--bg-color-primary);
  border: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav__toggle::before,
.main-nav__toggle::after {
  width: 24px;
  height: 10px;
  border-top: 2px solid var(--black);
  border-bottom: 2px solid var(--black);
  content: "";
  transition: all 0.3s 0.1s;
}
.main-nav__toggle::after {
  margin-top: -2px;
}
.main-nav__list {
  display: flex;
}
@media (max-width: 767px) {
  .main-nav__list {
    flex-direction: column;
    width: 100%;
    background-color: var(--bg-color-primary);
  }
}
@media (min-width: 768px) {
  .main-nav__list {
    flex-wrap: wrap;
    justify-content: flex-end;
  }
}
@media (max-width: 767px) {
  .main-nav__item {
    border-bottom: 1px solid var(--border-color);
  }
  .main-nav__item:first-child {
    border-top: 1px solid var(--border-color);
  }
}
@media (min-width: 768px) {
  .main-nav__item {
    margin-left: 15px;
  }
}
.main-nav__link {
  position: relative;
  display: block;
  padding: 22px 10px 23px;
  color: var(--black);
}
@media (min-width: 768px) {
  .main-nav__link {
    padding: 9px 10px;
  }
}
@media (min-width: 1280px) {
  .main-nav__link {
    padding: 10px;
  }
}
.main-nav__link:hover:not(.main-nav__link--current) {
  opacity: 0.6;
}
.main-nav__link:active {
  opacity: 0.3;
}
@media (max-width: 767px) {
  .main-nav__link--current {
    color: var(--color-main-dark);
  }
}
@media (min-width: 768px) {
  .main-nav__link--current::after {
    position: absolute;
    bottom: 0;
    left: 10px;
    right: 10px;
    height: 2px;
    background-color: var(--color-main);
    content: "";
  }
}
@media (min-width: 1280px) {
  .main-nav__link--current::after {
    background-color: var(--white);
  }
}
.page-main__header {
  padding-top: 27px;
  padding-bottom: 37px;
}
@media (min-width: 768px) {
  .page-main__header {
    padding-top: 39px;
    padding-bottom: 53px;
  }
}
@media (min-width: 1280px) {
  .page-main__header {
    padding-top: 44px;
    padding-bottom: 55px;
  }
}
@media (min-width: 768px) {
  .page-main--catalog .page-main__header {
    padding-bottom: 59px;
  }
}
@media (min-width: 1280px) {
  .page-main--catalog .page-main__header {
    padding-bottom: 62px;
  }
}
.page-main__title {
  font-family: "Oswald", "Arial", "Helvetica", sans-serif;
  font-size: 36px;
  font-weight: 400;
  line-height: 1.05;
  color: var(--text-color-secondary);
  text-transform: none;
  margin: 0;
  padding: 0;
}
@media (min-width: 768px) {
  .page-main__title {
    padding: 0 40px;
    font-size: 60px;
  }
}
@media (min-width: 1280px) {
  .page-main__title {
    padding: 0;
  }
}
.page-main--catalog .page-main__title {
  padding: 0;
}
.hero {
  position: relative;
  display: flex;
  flex-direction: column;
  font-family: "Oswald", "Arial", "Helvetica", sans-serif;
  line-height: 1;
}
@media (max-width: 767px) {
  .hero {
    align-items: center;
    min-height: 302px;
    padding-top: 20px;
    padding-bottom: 20px;
    text-align: center;
    color: var(--white);
  }
  .hero::before {
    position: absolute;
    z-index: -1;
    content: "";
    top: 0;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    max-width: 453px;
    height: 302px;
    background: no-repeat 50% 0;
    background-size: auto 302px;
    background-image: linear-gradient(to bottom, rgba(var(--color-main-rgb), 0.85) 302px, transparent 302px), url("../img/content/hero-bg-mobile.jpg");
  }
}
@media (max-width: 767px) and (min-resolution: 1.5dppx) {
  .hero::before {
    background-image: linear-gradient(to bottom, rgba(var(--color-main-rgb), 0.85) 302px, transparent 302px), url("../img/content/hero-bg-mobile@2x.jpg");
  }
}
@media (min-width: 1280px) {
  .hero {
    min-height: 553px;
    margin-bottom: 82px;
  }
}
.hero__title {
  margin: 7px 0 25px 0;
  font-size: 36px;
  font-weight: normal;
}
@media (min-width: 768px) {
  .hero__title {
    font-size: 60px;
    margin: 40px 0 41px 87px;
  }
}
@media (min-width: 1280px) {
  .hero__title {
    font-size: 60px;
    margin: 82px 0 40px 80px;
  }
}
.hero__slogan {
  margin: 0 0 29px 0;
  font-size: 14px;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .hero__slogan {
    font-size: 20px;
    margin: 0 0 49px 89px;
  }
}
@media (min-width: 1280px) {
  .hero__slogan {
    margin: 0 0 52px 80px;
  }
}
.hero__img {
  display: block;
}
@media (max-width: 767px) {
  .hero__img {
    width: 280px;
  }
}
@media (min-width: 768px) {
  .hero__img {
    position: relative;
    z-index: -1;
    width: 709px;
    margin: -83px auto 0;
  }
}
@media (min-width: 1280px) {
  .hero__img {
    position: absolute;
    top: 0;
    left: 50%;
    margin: 0 0 0 -162px;
    width: 552px;
  }
}
.hero__button {
  font-size: 20px;
  width: max-content;
}
@media (max-width: 767px) {
  .hero__button {
    order: 4;
    width: 100%;
    margin-top: 3px;
    font-size: 14px;
  }
}
@media (min-width: 768px) {
  .hero__button {
    margin-left: 90px;
    padding: 15px 26px 14px;
  }
}
@media (min-width: 1280px) {
  .hero__button {
    margin-left: 80px;
  }
}
.programs__list {
  display: grid;
  row-gap: 20px;
}
@media (min-width: 768px) {
  .programs__list {
    row-gap: 30px;
  }
}
@media (min-width: 1280px) {
  .programs__list {
    grid-template-columns: 1fr 1fr;
    column-gap: 80px;
  }
}
.programs__item {
  display: grid;
  grid-template-columns: 50px 1fr;
  align-items: center;
  gap: 22px 20px;
  padding: 21px 21px 6px;
  background-color: var(--bg-color-secondary);
}
@media (min-width: 768px) {
  .programs__item {
    grid-template-columns: 1fr 200px;
    grid-template-rows: repeat(3, auto);
    gap: 14px 53px;
    padding: 67px 62px 38px 53px;
  }
}
@media (min-width: 1280px) {
  .programs__item {
    grid-template-columns: 100px 1fr;
    grid-template-rows: 100px auto auto;
    gap: 39px 62px;
    padding: 47px 51px 41px;
  }
}
.programs__item::before {
  width: 50px;
  height: 50px;
  background-color: var(--color-main);
  border-radius: 50%;
  content: "";
}
@media (min-width: 768px) {
  .programs__item::before {
    grid-column: 2 / -1;
    grid-row: 1 / -1;
    align-self: center;
    width: 200px;
    height: 200px;
    margin-bottom: 15px;
  }
}
@media (max-width: 767px), (min-width: 1280px) {
  .programs__item::before {
    grid-column: 1 / 2;
    grid-row: 1 / 2;
    align-self: end;
  }
}
@media (min-width: 1280px) {
  .programs__item::before {
    width: 100px;
    height: 100px;
    margin: 0;
  }
}
.programs__item::after {
  justify-self: center;
  background: no-repeat;
  content: "";
}
@media (min-width: 768px) {
  .programs__item::after {
    grid-column: 2 / -1;
    grid-row: 1 / -1;
    align-self: center;
  }
}
@media (max-width: 767px), (min-width: 1280px) {
  .programs__item::after {
    grid-column: 1 / 2;
    grid-row: 1 / 2;
  }
}
.programs__slimming::after {
  width: 36px;
  height: 50px;
  margin: 0 0 3px 2px;
  background-image: url("../img/icons/cat-slim.svg");
}
@media (min-width: 768px) {
  .programs__slimming::after {
    width: 140px;
    height: 194px;
    margin-bottom: 43px;
  }
}
@media (min-width: 1280px) {
  .programs__slimming::after {
    width: 70px;
    height: 97px;
    margin-bottom: 15px;
  }
}
@media (min-width: 768px) {
  .programs__weight-gain::before {
    margin: 0 0 19px 2px;
  }
}
@media (min-width: 1280px) {
  .programs__weight-gain::before {
    margin: 0 0 0 1px;
  }
}
.programs__weight-gain::after {
  width: 67px;
  height: 28px;
  margin-left: -4px;
  background-image: url("../img/icons/cat-pro.svg");
}
@media (min-width: 768px) {
  .programs__weight-gain::after {
    width: 268px;
    height: 112px;
    margin: 0 0 20px;
  }
}
@media (min-width: 1280px) {
  .programs__weight-gain::after {
    width: 134px;
    height: 56px;
    margin: 0 0 4px -4px;
  }
}
.programs__title {
  font-family: "Oswald", "Arial", "Helvetica", sans-serif;
  font-size: 24px;
  font-weight: 400;
  line-height: 1.6;
  color: var(--text-color-secondary);
  text-transform: uppercase;
  margin: 0;
  padding: 0;
}
@media (min-width: 768px) {
  .programs__title {
    font-size: 36px;
  }
}
.programs__text {
  margin: 0;
  padding: 0 0 22px 0;
}
@media (max-width: 767px) {
  .programs__text {
    border-bottom: 1px solid #d9d9d9;
  }
}
@media (max-width: 767px), (min-width: 1280px) {
  .programs__text {
    grid-column: 1 / -1;
  }
}
@media (min-width: 768px) {
  .programs__text {
    font-size: 16px;
    line-height: 1.5;
    border: none;
    padding-bottom: 20px;
  }
}
.programs__link {
  position: relative;
  font-family: "Oswald", "Arial", "Helvetica", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1;
  color: var(--text-color-secondary);
  text-transform: uppercase;
  width: fit-content;
  margin-top: -19px;
  padding: 16px 45px 16px 0;
}
@media (max-width: 767px), (min-width: 1280px) {
  .programs__link {
    grid-column: 1 / -1;
  }
}
@media (min-width: 768px) {
  .programs__link {
    font-size: 20px;
    margin-top: 0;
    padding: 23px 55px 23px 0;
  }
}
